import { Input } from 'antd';
import React, { useEffect, useState } from 'react';
import { useAppDispatch, useAppSelector } from '../app/hooks';
import {
  searchStationQueryAsync,
  selectDestinationSearchText,
  selectOriginSearchText,
  selectScreen,
  selectStatus,
  setOriginSearchText,
  setDestinationSearchText,
} from '../features/station/stationSlice';

import './SearchBox.css'

const { Search } = Input;

function SearchBox() {
  const dispatch = useAppDispatch();
  const screen = useAppSelector(selectScreen);
  const loading = useAppSelector(selectStatus);
  const originSearchText = useAppSelector(selectOriginSearchText);
  const destinationSearchText = useAppSelector(selectDestinationSearchText);

  const [searcText, setSearchText] = useState('');

  const onSearch = (value: string) => {
    dispatch(searchStationQueryAsync(value.trim().toLowerCase()));
    switch (screen) {
      case 'origin':
        dispatch(setOriginSearchText(value.trim().toLowerCase()));
        break;
      case 'destination':
        dispatch(setDestinationSearchText(value.trim().toLowerCase()));
        break;
    }
  };

  const onChange = (event: { target: { value: string; }; }) => {
    setSearchText(event.target.value.trim().toLowerCase());
  };

  useEffect(() => {
    const searchText =
      screen === 'origin'
        ? originSearchText
        : screen === 'destination'
        ? destinationSearchText
        : '';
    setSearchText(searchText);
  }, [screen, originSearchText, destinationSearchText]);

  return (
    <Search
      className='search-box'
      loading={loading === 'loading'}
      placeholder='search text'
      onSearch={onSearch}
      onChange={onChange}
      hidden={screen === 'journey'}
      value={searcText}
    />
  );
}
export default SearchBox;
