import { ILocation } from '../../types/ILocation';
import { BASE_URL, QUERY_URL, NEARBY_URL, DUMMY_STOPS } from '../../util/AppConst';

// An async request for getting the list of stations/stops using search text
export function fetchStation(query = 'berlin') {
  const queryUrl = QUERY_URL.replace('ph_query', query);
  return (
    fetch(BASE_URL + queryUrl)
      // .then( response => response.json())
      .then((response) => {
        if (response.status === 200) {
          return response.json();
        } else {
          return DUMMY_STOPS;
        }
      })
      .catch(() => {
        return DUMMY_STOPS;
      })
  );
}

// An async request for getting the list of stations/stops using Geolocation
export function fetchStationGeolocation(location: ILocation) {
  const queryUrl = NEARBY_URL.replace('ph_latitude', location.latitude.toString()).replace(
    'ph_longitude',
    location.longitude.toString(),
  );
  return (
    fetch(BASE_URL + queryUrl)
      // .then( response => response.json())
      .then((response) => {
        if (response.status === 200) {
          return response.json();
        } else {
          return DUMMY_STOPS;
        }
      })
      .catch(() => {
        return DUMMY_STOPS;
      })
  );
}
