import { Button, Modal } from 'antd';
import React, { useState, useRef } from 'react';

import { GlobalOutlined } from '@ant-design/icons';

import { useAppDispatch, useAppSelector } from '../app/hooks';
import {
  searchStationGeolocationAsync,
  selectScreen,
  selectStatus,
} from '../features/station/stationSlice';
import { ILocation } from '../types/ILocation';

const suffix = (
  <GlobalOutlined
    style={{
      fontSize: 16,
      color: '#1734D8',
    }}
  />
);

function GeoLocation() {
  const screen = useAppSelector(selectScreen);
  const dispatch = useAppDispatch();
  const loading = useAppSelector(selectStatus);

  const [isModalOpen, setIsModalOpen] = useState(false);

  const msg = useRef('');

  const showModal = () => {
    setIsModalOpen(true);
  };

  const handleOk = () => {
    setIsModalOpen(false);
  };

  const handleCancel = () => {
    setIsModalOpen(false);
  };

  const locationSuccessCB = (position: { coords: { latitude: number; longitude: number; }; }): void => {
    if (position) {
      const location: ILocation = {
        type: 'location',
        latitude: position.coords.latitude,
        longitude: position.coords.longitude,
        // latitude: 52.520008,
        // longitude: 13.404954
      };
      dispatch(searchStationGeolocationAsync(location));
    } else {
      msg.current = 'location permission is needed';
      showModal();
    }
  };

  const locationErrCB = (err: { message: string; }): void => {
    msg.current = err.message;
    showModal();
  };

  const onClick = () => {
    navigator.geolocation.getCurrentPosition(locationSuccessCB, locationErrCB);
  };

  return screen === 'origin' ? (
    <>
      <Button
        type='text'
        size='large'
        icon={suffix}
        loading={loading === 'geo_loading'}
        onClick={() => onClick()}
        block
      >
        Use Location
      </Button>
      <Modal title={screen} open={isModalOpen} onOk={handleOk} onCancel={handleCancel}>
        <p>{msg.current}</p>
      </Modal>
    </>
  ) : null;
}
export default GeoLocation;
