import { createAsyncThunk, createSlice } from '@reduxjs/toolkit';
import { RootState } from '../../app/store';
import { fetchJourneys } from './journeyAPI';
import { DUMMY_JOURNEYS } from '../../util/AppConst';
import { IJourneyPayload } from '../../types/IJourneyPayload';
import { IJourenyState } from '../../types/IJourneyState';

const initialState: IJourenyState = {
  journeys: DUMMY_JOURNEYS,
  status: 'idle',
  results: 5,
};

// The function below is called a thunk and allows us to perform async logic. It
// can be dispatched like a regular action: `dispatch(searchJourneysAsync({fromStation: '232342', toStation: '23421'}))`. This
// will call the thunk with the `dispatch` function as the first argument. Async
// code can then be executed and other actions can be dispatched. Thunks are
// typically used to make async requests.
export const searchJourneysAsync = createAsyncThunk(
  'journey/fetchJourneys',
  async (journeyPayload: IJourneyPayload) => {
    const data = await fetchJourneys(journeyPayload);
    // The value we return becomes the `fulfilled` action payload
    return data;
  },
);

export const journeySlice = createSlice({
  name: 'journeys',
  initialState,
  // The `reducers` field lets us define reducers and generate associated actions
  reducers: {
    searchJourneys: (state) => {
      // Redux Toolkit allows us to write "mutating" logic in reducers. It
      // doesn't actually mutate the state because it uses the Immer library,
      state.journeys = DUMMY_JOURNEYS;
    },
  },
  // The `extraReducers` field lets the slice handle actions defined elsewhere,
  // including actions generated by createAsyncThunk or in other slices.
  extraReducers: (builder) => {
    builder
      .addCase(searchJourneysAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(searchJourneysAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.journeys = [...action.payload.journeys];
      })
      .addCase(searchJourneysAsync.rejected, (state) => {
        state.status = 'failed';
      });
  },
});

// export const { setOriginSelectedId, setDestinationSelectedId  } = journeySlice.actions;

// The function below is called a selector and allows us to select a value from
// the state. Selectors can also be defined inline where they're used instead of
// in the slice file. For example: `useSelector((state: RootState) => state.station.value)`
export const selectJourneys = (state: RootState) => state.journey.journeys;
export const selectJourneyStatus = (state: RootState) => state.journey.status;
// export const selectDestinationSelectedId = (state: RootState) => state.station.destinationSelectedId;

export default journeySlice.reducer;
