import { IJourneyPayload } from '../../types/IJourneyPayload';
import { BASE_URL, DUMMY_STOPS, JOURNEYS_URL } from '../../util/AppConst';

// An async request for getting the list of journeys using origin and destination stations/stops Id
export function fetchJourneys(journeyPayload: IJourneyPayload) {
  const journeyUrl = JOURNEYS_URL.replace('ph_from', journeyPayload.fromStation)
    .replace('ph_to', journeyPayload.toStation)
    .replace('ph_departure', journeyPayload.departure);
  return fetch(BASE_URL + journeyUrl)
    .then((response) => {
      if (response.status === 200) {
        return response.json();
      } else {
        return DUMMY_STOPS;
      }
    })
    .catch(() => {
      return DUMMY_STOPS;
    });
}
