import React from 'react';

import { ORGIN_TITLE, DESTINATION_TITLE, JOURNEY_TITLE } from '../util/AppConst';
import { useAppSelector } from '../app/hooks';
import { selectScreen } from '../features/station/stationSlice';

import './Title.css';

function Title() {
  const screen = useAppSelector(selectScreen);
  return (
    <h1 className='box-title'>
      {screen === 'origin'
        ? ORGIN_TITLE
        : screen === 'destination'
        ? DESTINATION_TITLE
        : screen === 'journey'
        ? JOURNEY_TITLE
        : ''}
    </h1>
  );
}
export default Title;
