import React from 'react';
import { Row } from 'antd';
import './CommonRow.css';

type RowProps = {
  justify?:
    | 'start'
    | 'end'
    | 'center'
    | 'space-around'
    | 'space-between'
    | 'space-evenly'
    | undefined;
  children?: React.ReactNode;
};

function CommonRow({ justify, children }: RowProps) {
  if (!justify) {
    justify = 'space-around';
  }
  return (
    <Row justify={justify} className='common-row' align={'middle'}>
      {children}
    </Row>
  );
}
export default CommonRow;
