import { Col, Row } from 'antd';
import React, { useState, useEffect } from 'react';
import { useAppDispatch, useAppSelector } from '../app/hooks';

import {
  assignStationsList,
  selectDestinationSelectedId,
  selectOriginSelectedId,
  selectScreen,
  selectStation,
  setDestinationSelectedId,
  setOriginSelectedId,
} from '../features/station/stationSlice';

import { SELECT_DESTINATION_STATION, SELECT_ORGIN_STATION } from '../util/AppConst';

import './LocationSelect.css';

function LocationSelect() {
  const dispatch = useAppDispatch();
  const stations = useAppSelector(selectStation);
  const screen = useAppSelector(selectScreen);
  const originSelectedId = useAppSelector(selectOriginSelectedId)
  const destinationSelectedId = useAppSelector(selectDestinationSelectedId)

  const [ selectedId, setSelectedId ] = useState('0')

  useEffect(() => {
    const selectedId =  screen === 'origin'
    ? originSelectedId
    : screen === 'destination'
    ? destinationSelectedId
    : '0'
    dispatch(assignStationsList())
    setSelectedId(selectedId)
  }, [screen, originSelectedId, destinationSelectedId])

  const onChange = (e: { target: { value: string } }) => {
    const selectedId = e.target.value;
    if (screen === 'origin') {
      dispatch(setOriginSelectedId(selectedId));
    } else if (screen === 'destination') {
      dispatch(setDestinationSelectedId(selectedId));
    }
  };

  const options = stations.filter(stop => stop.id !== null).map((stop) => (
    <option key={stop.id} value={stop.id}>
      {stop.name}
    </option>
  ));
  return (
    <Row align='middle'>
      <Col xs={{ span: 24 }} lg={{ span: 12 }}>
        {screen === 'origin'
          ? SELECT_ORGIN_STATION
          : screen === 'destination'
          ? SELECT_DESTINATION_STATION
          : ''}
      </Col>
      <Col xs={{ span: 24 }} lg={{ span: 12 }}>
        <select className='location-select' hidden={screen === 'journey'} onChange={onChange} value={selectedId}>
          {options}
        </select>
      </Col>
    </Row>
  );
}
export default LocationSelect;
