import { createAsyncThunk, createSlice, PayloadAction } from '@reduxjs/toolkit';
import { RootState } from '../../app/store';
import { IStationState } from '../../types/IStationState';
import { fetchStation, fetchStationGeolocation } from './stationAPI';
import { DUMMY_STOPS } from '../../util/AppConst';
import { ILocation } from '../../types/ILocation';

const initialState: IStationState = {
  stations: DUMMY_STOPS,
  stationsOrigin: DUMMY_STOPS,
  stationsDestination: DUMMY_STOPS,
  query: '',
  status: 'idle',
  screen: 'origin',
  originSearchText: '',
  destinationSearchText: '',
  originSelectedId: '',
  destinationSelectedId: '',
  departure: '',
};

// The function below is called a thunk and allows us to perform async logic. It
// can be dispatched like a regular action: `dispatch(searchQueryAsync(10))`. This
// will call the thunk with the `dispatch` function as the first argument. Async
// code can then be executed and other actions can be dispatched. Thunks are
// typically used to make async requests.
export const searchStationQueryAsync = createAsyncThunk(
  'station/fetchStation',
  async (query: string) => {
    const data = await fetchStation(query);
    // The value we return becomes the `fulfilled` action payload
    return data;
  },
);

export const searchStationGeolocationAsync = createAsyncThunk(
  'station/fetchStationGeolocation',
  async (location: ILocation) => {
    const data = await fetchStationGeolocation(location);
    // The value we return becomes the `fulfilled` action payload
    return data;
  },
);

export const stationSlice = createSlice({
  name: 'station',
  initialState,
  // The `reducers` field lets us define reducers and generate associated actions
  reducers: {
    assignStationsList: (state) => {
      // Redux Toolkit allows us to write "mutating" logic in reducers. It
      // doesn't actually mutate the state because it uses the Immer library,
      switch (state.screen) {
        case 'origin':
          state.stations = [...state.stationsOrigin];
          break;
        case 'destination':
          state.stations = [...state.stationsDestination];
          break;
      }
    },
    nextScreen: (state, action: PayloadAction<string>) => {
      switch (action.payload) {
        case 'origin':
          state.screen = 'destination';
          break;
        case 'destination':
          state.screen = 'journey';
          break;
        default:
          state.screen = 'none';
          break;
      }
    },
    preScreen: (state, action: PayloadAction<string>) => {
      switch (action.payload) {
        case 'journey':
          state.screen = 'destination';
          break;
        case 'destination':
          state.screen = 'origin';
          break;
        default:
          state.screen = 'none';
          break;
      }
    },
    setOriginSearchText: (state, action: PayloadAction<string>) => {
      state.originSearchText = action.payload;
    },
    setDestinationSearchText: (state, action: PayloadAction<string>) => {
      state.destinationSearchText = action.payload;
    },
    setOriginSelectedId: (state, action: PayloadAction<string>) => {
      state.originSelectedId = action.payload;
    },
    setDestinationSelectedId: (state, action: PayloadAction<string>) => {
      state.destinationSelectedId = action.payload;
    },
    setDeparture: (state, action: PayloadAction<string>) => {
      state.departure = action.payload;
    },
  },
  // The `extraReducers` field lets the slice handle actions defined elsewhere,
  // including actions generated by createAsyncThunk or in other slices.
  extraReducers: (builder) => {
    builder
      .addCase(searchStationQueryAsync.pending, (state) => {
        state.status = 'loading';
      })
      .addCase(searchStationQueryAsync.fulfilled, (state, action) => {
        state.status = 'idle';
        state.stations = [...DUMMY_STOPS, ...action.payload];
        switch (state.screen) {
          case 'origin':
            state.stationsOrigin = [...DUMMY_STOPS, ...action.payload];
            break;
          case 'destination':
            state.stationsDestination = [...DUMMY_STOPS, ...action.payload];
            break;
        }
      })
      .addCase(searchStationQueryAsync.rejected, (state) => {
        state.status = 'failed';
      })
      .addCase(searchStationGeolocationAsync.pending, (state) => {
        state.status = 'geo_loading';
      })
      .addCase(searchStationGeolocationAsync.fulfilled, (state, action) => {
        state.status = 'geo_idle';
        state.stations = [...DUMMY_STOPS, ...action.payload];
      })
      .addCase(searchStationGeolocationAsync.rejected, (state) => {
        state.status = 'geo_failed';
      });
  },
});

export const {
  assignStationsList,
  nextScreen,
  preScreen,
  setOriginSearchText,
  setDestinationSearchText,
  setOriginSelectedId,
  setDestinationSelectedId,
  setDeparture,
} = stationSlice.actions;

// The function below is called a selector and allows us to select a value from
// the state. Selectors can also be defined inline where they're used instead of
// in the slice file. For example: `useSelector((state: RootState) => state.station.value)`
export const selectStation = (state: RootState) => state.station.stations;
export const selectScreen = (state: RootState) => state.station.screen;
export const selectStatus = (state: RootState) => state.station.status;
export const selectOriginSearchText = (state: RootState) => state.station.originSearchText;
export const selectDestinationSearchText = (state: RootState) =>
  state.station.destinationSearchText;
export const selectOriginSelectedId = (state: RootState) => state.station.originSelectedId;
export const selectDestinationSelectedId = (state: RootState) =>
  state.station.destinationSelectedId;
export const selectDeparture = (state: RootState) => state.station.departure;

export default stationSlice.reducer;
