import React from 'react';
import { DatePicker } from 'antd';
import type { DatePickerProps } from 'antd/es/date-picker';
import dayjs from 'dayjs';
import { useAppDispatch, useAppSelector } from '../app/hooks';
import { selectScreen, setDeparture } from '../features/station/stationSlice';

import './DateTimePicker.css'

function DateTimePicker() {
  const dispatch = useAppDispatch();
  const screen = useAppSelector(selectScreen);

  const onDepartureSelect = (value: DatePickerProps['value']) => {
    const departure = dayjs(value).format('YYYY-MM-DDTHH:mm:ss');
    dispatch(setDeparture(departure));
  };

  return screen === 'origin' ? <DatePicker className='date-picker' showTime onOk={onDepartureSelect} /> : null;
}
export default DateTimePicker;
