import React from 'react';

import { selectJourneys, selectJourneyStatus } from '../features/journey/journeySlice';
import { useAppSelector } from '../app/hooks';

import { JOURNEY_COLUMNS, LEG_COLUMNS } from '../util/AppConst';
import { Col, Table } from 'antd';
import { IJourneyDataType } from '../types/IJourneyDataType';
import CommonRow from './CommonRow';

import './Journey.css';
import dayjs from 'dayjs';

function Journey() {
  const journeys = useAppSelector(selectJourneys);
  const loading = useAppSelector(selectJourneyStatus);
  const dataJourney: IJourneyDataType[] = journeys.map((item, i) => {
    return {
      key: i,
      type: item.type.toUpperCase() + ' ' + (i + 1),
      legs: item.legs.length,
      price: item.price?.amount || 'N/A',
      currency: item.price?.currency || 'N/A',
    };
  });

  const legRowRender = (record: { key: number }) => {
    const legs = journeys.at(record.key)?.legs;
    const data = legs?.map((item, i) => {
      return {
        key: i,
        origin: item?.origin.name,
        destination: item?.destination.name,
        departure: dayjs(item.departure).format('DD MMM hh:mm a'),
        arrival: dayjs(item.arrival).format('DD MMM hh:mm a'),
        direction: item.direction,
        cancelled: item.cancelled ? 'Yes' : 'No',
      };
    });
    return (
      <Table dataSource={data} columns={LEG_COLUMNS} pagination={false} scroll={{ x: true }} />
    );
  };

  return (
    <>
      <CommonRow>
        <Col span={24}>
          {journeys.length > 0 && journeys.at(0)?.type !== 'No Journeys' ? (
            <Table
              showHeader={true}
              rowClassName={(record, index) =>
                index % 2 === 0 ? 'table-row-light' : 'table-row-dark'
              }
              dataSource={dataJourney}
              columns={JOURNEY_COLUMNS}
              expandedRowRender={legRowRender}
              pagination={{ position: ['bottomCenter'] }}
              bordered={true}
              scroll={{ x: true }}
              loading={loading === 'loading'}
            />
          ) : (
            <Table loading={loading === 'loading'} />
          )}
        </Col>
      </CommonRow>
    </>
  );
}

export default Journey;
