import { Button, Modal } from 'antd';
import React, { useState } from 'react';

import { useAppSelector, useAppDispatch } from '../app/hooks';
import {
  selectScreen,
  selectOriginSelectedId,
  selectDestinationSelectedId,
  nextScreen,
  selectDeparture,
} from '../features/station/stationSlice';
import { searchJourneysAsync } from '../features/journey/journeySlice';
import { IJourneyPayload } from '../types/IJourneyPayload';

import './NextButton.css'

function NextButton() {
  const [isModalOpen, setIsModalOpen] = useState(false);

  const showModal = () => {
    setIsModalOpen(true);
  };

  const handleOk = () => {
    setIsModalOpen(false);
  };

  const handleCancel = () => {
    setIsModalOpen(false);
  };

  const screen = useAppSelector(selectScreen);
  const selectedOriginId = useAppSelector(selectOriginSelectedId);
  const selectedDestinationId = useAppSelector(selectDestinationSelectedId);
  const selectedDeparture = useAppSelector(selectDeparture);
  const dispatch = useAppDispatch();

  const onClick = () => {
    if (
      screen === 'origin' &&
      (selectedOriginId === '0' || selectedOriginId === '' || selectedDeparture === '')
    ) {
      showModal();
    } else if (
      screen === 'destination' &&
      (selectedDestinationId === '0' || selectedDestinationId === '' || selectedDeparture === '')
    ) {
      showModal();
    } else if (
      screen === 'destination' &&
      selectedOriginId !== '' &&
      selectedDestinationId !== '' &&
      selectedDeparture !== ''
    ) {
      const journeyPayload: IJourneyPayload = {
        fromStation: selectedOriginId,
        toStation: selectedDestinationId,
        departure: selectedDeparture,
      };
      dispatch(searchJourneysAsync(journeyPayload));
      dispatch(nextScreen(screen));
    } else {
      dispatch(nextScreen(screen));
    }
  };

  return (
    <>
      {
        <Button
          className='next-button'
          size='large'
          onClick={() => onClick()}
          block
          disabled={screen === 'journey'}
        >
          {screen === 'destination' || screen === 'journey' ? 'Show' : 'Next'}
        </Button>
      }
      <Modal title={screen} open={isModalOpen} onOk={handleOk} onCancel={handleCancel}>
        <p>{`Please select ${screen} and departure time.`}</p>
      </Modal>
    </>
  );
}
export default NextButton;
